<template>
  <div id="deal">
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
          <router-link to="/admin/record/deal" class="pull-right btn btn-primary" style="margin-left: 20px">
          </router-link>
          <span @click="_$power(onload,'boss_trade_export')" download="" class="btn btn-primary" style="float: right"></span>
        </div>
        <div class="box-body">
          <!---->
          <ul class="search">
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.businessOrderNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.orderNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.sn" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.merchantName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.proxyName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.proxyName1" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-date-picker
                style="width: 188px"
                v-model="date"
                type="daterange"
                align="right"
                placeholder=""
                :picker-options="pickerOptions2" size="small">
              </el-date-picker>
            </li>
            <li class="same">
              <label>:</label>
              <div class="form-control price" style="margin-top: -4px;">
                <input type="text" name="date" value="" v-model="query.lessTotalFee">
                <input type="text" name="date" value="" v-model="query.moreTotalFee">
              </div>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 188px" clearable v-model="query.status" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-select style="width: 188px" clearable v-model="query.settleStatus" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label></label>
              <el-select style="width: 188px" clearable v-model="query.payType" size="small">
                <el-option label="" value=""></el-option>
                <el-option label="" value="wechat"></el-option>
                <el-option label="" value="alipay"></el-option>
                <el-option label="" value="unionpay"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label></label>
              <el-select style="width: 188px" clearable v-model="query.payChannelSign" size="small">
                <el-option label="" value=""></el-option>
                <el-option :label="channel.channelName" :value="channel.channelTypeSign" v-for="channel in channelList"></el-option>
              </el-select>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading" height="620" style="font-size: 12px;margin-bottom: 15px;" :data="records" border>
            <el-table-column width="62" label="" fixed="left" type="index"></el-table-column>
            <el-table-column prop="appId" label="" min-width="85"></el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="scope.row.orderNo" style="cursor: pointer" title="">{{scope.row.orderNo|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="scope.row.businessOrderNo" style="cursor: pointer" title="">{{scope.row.businessOrderNo|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="112">
              <template scope="scope">
                <span class="td" :data-clipboard-text="scope.row.sn" style="cursor: pointer" title="">{{scope.row.sn|changeHide}}</span>
              </template>
            </el-table-column>
            <el-table-column label="" width="162">
              <template scope="scope">
                <span>{{scope.row.createTime|changeTime}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="merchantName" label="" min-width="120"></el-table-column>
            <el-table-column prop="proxyName" label="" min-width="90"></el-table-column>
            <el-table-column prop="proxyName1" label="" min-width="110"></el-table-column>
            <el-table-column label="" align="right">
              <template scope="scope">
                <span>{{scope.row.tradeAmount|toFix}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="payRate" label="" min-width="90" align="right"></el-table-column>
            <el-table-column label="" min-width="90">
              <template scope="scope">
                <span>{{scope.row.status|changeStatus}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="settleStatus" :formatter="changeSettleStatus" label="" min-width="90"></el-table-column>
            <el-table-column prop="payType" label="" min-width="115"></el-table-column>
            <el-table-column prop="payChannelSigns" label="" min-width="115"></el-table-column>
            <el-table-column prop="remark" label="" min-width="90"></el-table-column>
            <el-table-column label="" width="90" fixed="right">
              <template scope="scope">
                <router-link :to="{path:'/admin/record/newDealDet',query:{orderNo:scope.row.orderNo}}" type="text">
                </router-link>
              </template>
            </el-table-column>
          </el-table>
          </el-table>
          <ul style="float: left;margin-top: 5px">
            <li>
              <label style="margin-right: 10px;"></label>
              <span>{{pageTotal}}&nbsp;</span>&nbsp;&nbsp;&nbsp;&nbsp;
              <span>{{addTotal}}&nbsp;</span>
            </li>
          </ul>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.page"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.size"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
        </div>
        <div class="box box-info mask el-message-box" v-if="isMask">
          <div class="maskCon">
            <div class="head">
              <div class="title"></div>
              <i class="el-icon-close" @click="isMask=false"></i>
            </div>
            <div class="body">
              <div></div>
            </div>
            <div class="foot">
              <a href="javascript:void(0)" @click="isMask=false" class="el-button el-button--default"></a>
              <a :href="'http://'+loadUrl" @click="isMask=false"
                 class="el-button el-button-default el-button--primary "></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="babel">
  import Clipboard from "clipboard"
  export default{
    name: 'deal',
    data(){
      return {
        query:{
          page:1,
          size:10,
          orderNo:'',
          businessOrderNo:'',
          sn:'',
          merchantName: '',
          startTime: '',
          endTime: '',
          lessTotalFee: '',
          moreTotalFee: '',
          status: '',
          settleStatus:'',
          payType:'',
          proxyName:'',
          proxyName1:'',
          loadUrl: '',
          loadUrl1: '',
          payChannelSign:''
        },
        channelList:[],
        date: '',
        records: [],
        count: 0,
        total: '',
        loading: true,
        url: '',
        pageTotal: 0,
        addTotal: 0,
        isMask:false
      }
    },
    created: function () {
      var clipboard = new Clipboard('.td');
      // 
      clipboard.on('success', (e) => {
        this.$message({
          showClose: true,
          message: "  " + e.text,
          type: 'success'
        });
      });

      this.$http.post('/admin/channel/list')
        .then(function (res) {
          this.channelList = res.data;
        }, function (err) {
          this.$message({
            showClose: true,
            message: err.statusMessage,
            type: 'error'
          });
        });

      let time = new Date();
      this.date = [time,time];
      for (var j = 0; j < this.date.length; j++) {
        var str = this.date[j];
        var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
        for (var i = 0, len = ary.length; i < len; i++) {
          if (ary[i] < 10) {
            ary[i] = '0' + ary[i];
          }
        }
        str = ary[0] + '-' + ary[1] + '-' + ary[2];
        if (j == 0) {
          this.query.startTime = str;
        } else {
          this.query.endTime = str;
        }
      }
      this.getData();
      this.getAddTotal()
    },
    methods: {
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/queryOrder/orderList',this.query)
          .then(function (res) {
            this.loading = false;
            this.records = res.data.records;
            this.loadUrl1 = res.data.ext;
            this.loading = false;
            this.count = res.data.count;
            var price=0;
            var toFix = function (val) {
              return parseFloat(val).toFixed(2)
            };
            for (var i = 0; i < this.records.length; i++) {
              price = toFix(parseFloat(price)+parseFloat(this.records[i].tradeAmount))
              if (this.records[i].payRate != null) {
                this.records[i].payRate = (parseFloat(this.records[i].payRate) * 100).toFixed(2) + '%';
              }
            }
            this.pageTotal = price;
          },function (err) {
            this.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })

      },
      onload: function () {
        this.$data.loadUrl = this.loadUrl1;
        this.$data.isMask = true;
      },
      changeSettleStatus: function (row, column) {
        var val = row.settleStatus;
        if(val == 2){
          return ''
        }else if(val == 1){
          return ''
        }else if(val == 3){
          return ''
        }
      },
      search(){
        this.total = '';
        this.query.page = 1;
        this.getData();
        this.getAddTotal()
      },
      //
      handleSizeChange(val) {
        this.query.page = 1;
        this.query.size = val;
        this.getData();
      },
      //
      handleCurrentChange(val) {
        this.query.page = val;
        this.getData()
      },
      tableFoot(row, index) {
        if (row.proxyName1 === ''||row.proxyName1 === '') {
          return {background:'#eef1f6'}
        }
        return '';
      },
      getAddTotal(){
        this.$http.post('/admin/queryOrder/amountCount',this.query)
          .then(res=>{
            this.addTotal = res.data;
          })
          .catch(err=>{
            this.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          });
      }
    },
    watch: {
      date: function (val, oldVal) {
        if (val!=undefined&&val[0] != null) {
          for (var j = 0; j < val.length; j++) {
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for (var i = 0, len = ary.length; i < len; i++) {
              if (ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if (j == 0) {
              this.query.startTime = str;
            } else {
              this.query.endTime = str;
            }
          }
        } else {
          this.query.startTime = '';
          this.query.endTime = '';
        }
      }
    },
    filters: {
      changeStatus: function (val) {
        if(val == 1){
          return ""
        }else if(val == 3){
          return ""
        }else if(val == 4){
          return ""
        }else if(val == 5){
          return ""
        }else if(val == 6){
          return ""
        }else if(val == 7){
          return ""
        }else if(val == 6){
          return ""
        }
      },
    }
  }
</script>

<style scoped lang="less" rel="stylesheet/less">
  ul {
    padding: 0;
    margin:0;
  }
  .search{
    label{
      display: block;
      margin-bottom: 0;
    }
  }

  .same {
    list-style: none;
    display: inline-block;
    margin: 0 15px 10px 0;
  }
  .btn{
    font-size: 12px;
  }

  .price {
    display: inline-block;
    width: 188px;
    height: 30px;
    border-radius: 4px;
    border-color: #bfcbd9;
    position: relative;
    top: 6px;
    input {
      border: none;
      display: inline-block;
      width: 43%;
      height: 25px;
      position: relative;
      top: -3px;
    }
  }

  .price:hover {
    border-color: #20a0ff;
  }
  .mask {
    z-index: 2020;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.45);

    .maskCon {
      margin: 250px auto;
      text-align: left;
      vertical-align: middle;
      background-color: #fff;
      width: 420px;
      border-radius: 3px;
      font-size: 16px;
      overflow: hidden;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;

      .head {
        position: relative;
        padding: 20px 20px 0;

        .title {
          padding-left: 0;
          margin-bottom: 0;
          font-size: 16px;
          font-weight: 700;
          height: 18px;
          color: #333;
        }

        i {
          font-family: element-icons !important;
          speak: none;
          font-style: normal;
          font-weight: 400;
          font-variant: normal;
          text-transform: none;
          vertical-align: baseline;
          display: inline-block;
          -webkit-font-smoothing: antialiased;
          position: absolute;
          top: 19px;
          right: 20px;
          color: #999;
          cursor: pointer;
          line-height: 20px;
          text-align: center;
        }

      }
      .body {
        padding: 30px 20px;
        color: #48576a;
        font-size: 14px;
        position: relative;

        div {
          margin: 0;
          line-height: 1.4;
          font-size: 14px;
          color: #48576a;
          font-weight: 400;
        }
      }
      .foot {
        padding: 10px 20px 15px;
        text-align: right;
      }
    }
  }
</style>



