<template>
  <div id="storeList">
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
          <span @click="synchro" class="btn btn-primary" style="color: #fff;float: right;" v-if="activeName=='first'"></span>
        </div>
        <div class="box-body">
          <el-tabs class="tab" v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
              <!---->
              <ul class="search">
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.markCode" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.merchantName" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.proxyName" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.proxyName1" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.mobile" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-date-picker style="width: 188px" v-model="date" type="daterange" align="right" placeholder="" :picker-options="pickerOptions" size="small">
                  </el-date-picker>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-date-picker style="width: 188px" v-model="date1" type="daterange" align="right" placeholder="" :picker-options="pickerOptions" size="small">
                  </el-date-picker>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-date-picker style="width: 188px" v-model="date2" type="daterange" align="right" placeholder="" :picker-options="pickerOptions" size="small">
                  </el-date-picker>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-select style="width: 188px" v-model="query.status" clearable placeholder="" size="small">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value="0"></el-option>
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="4"></el-option>
                  </el-select>
                </li>
                <li class="same">
                  <div class="btn btn-primary" @click="search"></div>
                </li>
                <li class="same" style="float: right">
                  <span @click="_$power(onload,'boss_merchant_export')" download="" class="btn btn-primary"></span>
                </li>
              </ul>
              <!---->
              <el-table v-loading.body="loading" style="font-size: 12px;margin-bottom:15px" :data="records" border>
                <el-table-column type="index" width="70" label=""></el-table-column>
                <el-table-column prop="markCode" label=""></el-table-column>
                <el-table-column prop="merchantName" label=""></el-table-column>
                <el-table-column prop="proxyName" label=""></el-table-column>
                <el-table-column prop="proxyName1" label=""></el-table-column>
                <el-table-column prop="createTime" :formatter="changeTime" label=""></el-table-column>
                <el-table-column prop="mobile" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <span v-if="records[scope.$index].source==0"></span>
                    <span v-if="records[scope.$index].source==1"></span>
                    <span v-if="records[scope.$index].source==2"></span>
                  </template>
                </el-table-column>
                <el-table-column prop="authenticationTime" label=""></el-table-column>
                <el-table-column prop="checkedTime" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <span v-if="records[scope.$index].status==0"></span>
                    <span v-if="records[scope.$index].status==1"></span>
                    <span v-if="records[scope.$index].status==2"></span>
                    <span v-if="records[scope.$index].status==3||records[scope.$index].status==6"></span>
                    <span v-if="records[scope.$index].status==4"></span>
                  </template>
                </el-table-column>
                <el-table-column label="" width="100">
                  <template scope="scope">
                    <a @click="_$power(scope.row.id,scope.row.status,audit,'boss_merchant_check')" v-if="records[scope.$index].status==2" type="text" size="small"></a>
                    <a @click="_$power(scope.row.id,scope.row.status,audit,'boss_merchant_detail')" v-if="records[scope.$index].status!=2" type="text" size="small"></a>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <!---->
              <ul class="search">
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.globalID" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <label>:</label>
                  <el-input style="width: 188px" v-model="query.shortName" placeholder="" size="small"></el-input>
                </li>
                <li class="same">
                  <div class="btn btn-primary" @click="search"></div>
                </li>
                <!--<li class="same" style="float: right">
                  <span @click="onload()" download="" class="btn btn-primary" style="color: #fff"></span>
                </li>-->
              </ul>
              <!---->
              <el-table v-loading.body="loading" style="font-size: 12px;margin-bottom:15px" :data="records" border>
                <el-table-column type="index" width="100" label=""></el-table-column>
                <el-table-column prop="globalID" label=""></el-table-column>
                <el-table-column prop="shortName" label=""></el-table-column>
                <el-table-column prop="proxyNames" label=""></el-table-column>
                <el-table-column prop="createTime" label="" :formatter="changeTime"></el-table-column>
                <el-table-column prop="cellphone" label=""></el-table-column>
                <el-table-column prop="districtCode" label=""></el-table-column>
                <el-table-column prop="industryCode" label=""></el-table-column>
                <el-table-column prop="stat" label=""></el-table-column>
                <el-table-column label="" width="100">
                  <template scope="scope">
                    <a @click="_$power(scope.row.id,scope.row.status,auditHsy,'boss_merchant_check')" v-if="records[scope.$index].stat==''" type="text" size="small"></a>
                    <a @click="_$power(scope.row.id,scope.row.status,auditHsy,'boss_merchant_detail')" v-if="records[scope.$index].stat!=''" type="text" size="small"></a>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.pageNo"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
          <div class="box box-info mask el-message-box" v-if="isMask">
            <div class="maskCon">
              <div class="head">
                <div class="title"></div>
                <i class="el-icon-close" @click="isMask=false"></i>
              </div>
              <div class="body">
                <div></div>
              </div>
              <div class="foot">
                <a href="javascript:void(0)" @click="isMask=false" class="el-button el-button--default"></a>
                <a :href="'http://'+loadUrl" @click="isMask=false" class="el-button el-button-default el-button--primary "></a>
              </div>
            </div>

          </div>
        </div>
        <!-- /.box-body -->
      </div>
      <!-- /.box -->
    </div>
  </div>
</template>

<script lang="babel">
  export default{
    name: 'storeList',
    data(){
      return {
        isMask: false,
        activeName: 'first', //
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        date:'',
        date1:'',
        date2:'',
        url:'',
        loadUrl:'',
        loadUrl1:'',
        fromName:'',
        query:{
          pageNo:1,
          pageSize:10,
          shortName:'',
          globalID:'',
          proxyName:'',
          mobile:'',
          proxyName1:'',
          startTime:'',
          endTime:'',
          startTime1:'',
          endTime1:'',
          startTime2:'',
          endTime2:'',
          status:''
        },
        records: [],
        count: 0,
        total: 0,
        currentPage: 1,
        loading: true,
      }
    },
    beforeRouteEnter (to, from, next) {
      next(vm=>{
        vm.$data.fromName = from.name;
        if(vm.$data.fromName=='StoreAuditHSY'){
          vm.$data.activeName='second';
          vm.$data.url='/admin/hsyMerchantList/getMerchantList'
        }else {
          vm.$data.activeName='first';
          vm.$data.url='/admin/query/getAll'
        }
        vm.$data.records = '';
        vm.$data.total = 0;
        vm.$data.count = 0;
        vm.$http.post(vm.$data.url,vm.$data.query)
          .then(function (res) {
            vm.$data.loading = false;
            vm.$data.records = res.data.records;
            vm.$data.total = res.data.totalPage;
            vm.$data.count = res.data.count;
            vm.$data.loadUrl1 = res.data.ext;
          }, function (err) {
            vm.$data.loading = false;
            vm.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      })
    },
    created: function () {
//      let time = new Date();
//      this.date = [time,time];
//      for (var j = 0; j < this.date.length; j++) {
//        var str = this.date[j];
//        var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
//        for (var i = 0, len = ary.length; i < len; i++) {
//          if (ary[i] < 10) {
//            ary[i] = '0' + ary[i];
//          }
//        }
//        str = ary[0] + '-' + ary[1] + '-' + ary[2];
//        if (j == 0) {
//          this.$data.query.startTime = str;
//        } else {
//          this.$data.query.endTime = str;
//        }
//      }
    },
    methods: {
      auditHsy: function (id,status) {
        this.$router.push({path:'/admin/record/StoreAuditHSY',query:{id:id,status:status}})
      },
      audit: function (id,status) {
        this.$router.push({path:'/admin/record/StoreAudit',query:{id:id,status:status}})
      },
      //
      synchro: function () {
        this.loading = true;
        this.$http.get('/admin/merchantIn/update')
          .then(res => {
            this.loading = false;
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
          })
          .catch(err => {
            this.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      onload: function () {
         this.$data.loadUrl = this.loadUrl1;
         this.$data.isMask = true;
      },
      //hss
      changeTime: function (row, column) {
        var val=row.createTime;
        if(val==''||val==null){
          return ''
        }else {
          val = new Date(val)
          var year=val.getFullYear();
          var month=val.getMonth()+1;
          var date=val.getDate();
          var hour=val.getHours();
          var minute=val.getMinutes();
          var second=val.getSeconds();
          function tod(a) {
            if(a<10){
              a = "0"+a
            }
            return a;
          }
          return year+"-"+tod(month)+"-"+tod(date)+" "+tod(hour)+":"+tod(minute)+":"+tod(second);
        }
      },
      //
      handleClick: function (tab,event) {
        this.$data.query={
          pageNo:1,
          pageSize:10,
          shortName:'',
          globalID:'',
          proxyName:'',
          proxyName1:'',
          mobile:'',
          startTime:'',
          endTime:'',
          startTime1:'',
          endTime1:'',
          startTime2:'',
          endTime2:'',
          status:''
        };
        if(event.target.innerHTML==""){
          this.$data.url='/admin/query/getAll'
        }else if(event.target.innerHTML==""){
          this.$data.url='/admin/hsyMerchantList/getMerchantList'
        }
        this.$data.records='';
        this.$data.loading = true;
        this.$http.post(this.$data.url,this.$data.query)
          .then(function (res) {
            this.$data.loading = false;
            this.$data.records   = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
            this.$data.loadUrl1 = res.data.ext;
          }, function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      },
      search(){
        this.$data.query.pageNo = 1;
        this.$data.loading = true;
        this.$http.post(this.$data.url,this.$data.query)
          .then(function (res) {
            this.$data.loading = false;
            this.$data.records   = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
          }, function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      },
      //
      handleCurrentChange(val) {
        this.$data.query.pageNo = val;
        this.$data.loading = true;
        this.$data.records = '';
        this.$http.post(this.$data.url,this.$data.query)
          .then(function (res) {
            this.$data.loading = false;
            this.$data.records   = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
          }, function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      },
      handleSizeChange: function (val) {
        this.query.pageNo = 1;
        this.query.pageSize = val;
        this.loading = true;
        this.$http.post(this.$data.url,this.$data.query)
          .then(function (res) {
            this.$data.loading = false;
            this.$data.records   = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
          }, function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      }
    },
    watch:{
      date:function (val,oldVal) {
        if(val!=undefined&&val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.startTime = str;
            }else {
              this.$data.query.endTime = str;
            }
          }
        }else {
          this.$data.query.startTime = '';
          this.$data.query.endTime = '';
        }
      },
      date1:function (val,oldVal) {
        if(val!=undefined&&val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.startTime1 = str;
            }else {
              this.$data.query.endTime1 = str;
            }
          }
        }else {
          this.$data.query.startTime1 = '';
          this.$data.query.endTime1 = '';
        }
      },
      date2:function (val,oldVal) {
        if(val!=undefined&&val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.startTime2 = str;
            }else {
              this.$data.query.endTime2 = str;
            }
          }
        }else {
          this.$data.query.startTime2 = '';
          this.$data.query.endTime2 = '';
        }
      },
    },
  }
</script>
<style scoped lang="less" rel="stylesheet/less">
  ul {
    padding: 0;
    margin:0;
  }
  .search{
    margin-bottom:0;
    label{
      display: block;
      margin-bottom: 0;
    }
  }
  .same{
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }

  .mask {
    z-index: 2020;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.45);
    .maskCon {
      margin: 250px auto;
      text-align: left;
      vertical-align: middle;
      background-color: #fff;
      width: 420px;
      border-radius: 3px;
      font-size: 16px;
      overflow: hidden;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      .head {
        position: relative;
        padding: 20px 20px 0;
        .title {
          padding-left: 0;
          margin-bottom: 0;
          font-size: 16px;
          font-weight: 700;
          height: 18px;
          color: #333;
        }
        i {
          font-family: element-icons !important;
          speak: none;
          font-style: normal;
          font-weight: 400;
          font-variant: normal;
          text-transform: none;
          vertical-align: baseline;
          display: inline-block;
          -webkit-font-smoothing: antialiased;
          position: absolute;
          top: 19px;
          right: 20px;
          color: #999;
          cursor: pointer;
          line-height: 20px;
          text-align: center;
        }
      }
      .body {
        padding: 30px 20px;
        color: #48576a;
        font-size: 14px;
        position: relative;
        div {
          margin: 0;
          line-height: 1.4;
          font-size: 14px;
          color: #48576a;
          font-weight: 400;
        }
      }
      .foot {
        padding: 10px 20px 15px;
        text-align: right;
      }
    }

  }
</style>



