<script lang="babel">
  export default {
    name: 'agentAddBase',
    data () {
      return {
        dialogFormVisible: false,
        password:'',
        provinces: '',
        citys: '',
        province: '',
        city: '',
        level: '',
        query: {
          mobile: '',
          name: '',
          loginName:'',
          loginPwd:'',
          email:'',
          belongProvinceCode:'',
          belongProvinceName:'',
          belongCityCode: '',
          belongCityName: '',
          belongArea: '',
          bankCard: '',
          bankAccountName: '',
          bankReserveMobile: '',
          idCard: '',
        },
        id: 0,
        isShow: true,
        productId: ''
      }
    },
    created: function () {
      //
      this.$http.post('/admin/district/findAllDistrict')
        .then(function (res) {
          this.$data.provinces = res.data;
        })
        .catch(function (err) {
          this.$message({
            showClose: true,
            message: err.statusMessage,
            type: 'error'
          })
        })
      //
      if (this.$route.query.id != undefined) {
        this.$data.isShow = false;
        this.$http.get('/admin/dealer/findBydealerId/' + this.$route.query.id)
          .then(function (res) {
            this.$data.query = res.data;
            this.$data.province = res.data.belongProvinceName;
            this.$data.city = res.data.belongCityName;
          })
      }
      this.$data.level = this.$route.query.level;
    },
    watch: {
      province: function (val, oldval) {
        if (val != oldval&&oldval!="") {
          this.$data.city = '';
          this.$data.query.province = this.$data.province;
        }
        if (this.$data.province != '') {
          for (var i = 0; i < this.$data.provinces.length; i++) {
            if (this.$data.provinces[i].name == this.$data.province) {
              this.$data.query.belongProvinceCode= this.$data.provinces[i].code
              this.$data.query.belongProvinceName= this.$data.province
              this.$data.citys = this.$data.provinces[i].cityList
            }
          }
        }
      },
      city: function (val, oldval) {
        if (val != oldval) {
          this.$data.query.belongCityName = this.$data.city;
          for(var i=0;i<this.$data.citys.length;i++){
            if(this.$data.citys[i].name == this.$data.city){
              this.$data.query.belongCityCode = this.$data.citys[i].code;
            }
          }
        }
      }
    },
    methods: {
      //
      resetPw:function() {
        this.$http.post('/admin/dealer/updatePwd',{dealerId:this.$route.query.id,loginPwd:this.$data.password})
          .then(function (res) {
            this.$data.dialogFormVisible = false;
            this.$data.password = '';
            this.$message({
              showClose: true,
              type: 'success',
              message: ''
            });
          })
          .catch(function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
        /*this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          this.$http.post('/admin/dealer/updatePwd',{dealerId:this.$route.query.id,loginPwd:value})
            .then(function (res) {
              this.$message({
                showClose: true,
                type: 'success',
                message: ''
              });
            })
            .catch(function (err) {
              this.$message({
                showClose: true,
                message: err.statusMessage,
                type: 'error'
              })
            })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });*/
      },
      //
      create: function () {
        this.$http.post('/admin/user/addFirstDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/admin/record/agentListFir')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      goBack: function () {
        if(this.$route.query.level==2){
          this.$router.push('/admin/record/agentListSec')
        }else {
          this.$router.push('/admin/record/agentListFir')
        }
      },
      //
      change: function () {
        this.$data.query.dealerId = this.$data.query.id;
        this.$http.post('/admin/user/updateDealer2', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            if(this.$route.query.level==2){
              this.$router.push('/admin/record/agentListSec')
            }else {
              this.$router.push('/admin/record/agentListFir')
            }
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      }
    },
    filters: {
      changeName: function (val) {
        if (val == 101) {
          val = ''
        } else if (val == 102) {
          val = ''
        } else if (val == 103) {
          val = ''
        }
        return val;
      }
    }
  }
</script>

