<script lang="babel">
  export default {
    name: 'passAdd',
    data () {
      return {
        query: {
          channelShortName:'',
          channelName: '',
          channelCode: '',
          supportWay: '',
          thirdCompany: '',
          channelSource: '',
          basicTradeRate: '',
          basicWithdrawFee: '',
          basicBalanceType: '',
          basicSettleType: '',
          limitAmount: '',
          limitMinAmount: '',
          lowestFee:'',
          isUse:'',
          isNeed: '',
          remarks: '',
          id: '',
          status: '',
          accountId: '',
          checkType:''
        },
        id: 0,
        isShow: true,
        name: '',
        payWay: [],
        nameType: ''
      }
    },
    created: function () {
      //
      if (this.$route.query.id != undefined) {
        this.$data.isShow = false;
        this.$http.post('/admin/channel/list')
          .then(function (res) {
            this.query = res.data[this.$route.query.id];
            this.name = res.data[this.$route.query.id].channelName;
            this.query.isNeed = res.data[this.$route.query.id].isNeed;
            this.query.isUse = res.data[this.$route.query.id].isUse;
            if (//.test(this.name)) {
              this.nameType = 'wx';
              if (this.query.supportWay == 3) {
                this.payWay = ['', '']
              } else if (this.query.supportWay == 2) {
                this.payWay = ['']
              } else if (this.query.supportWay == 1) {
                this.payWay = ['']
              }
            } else if (//.test(this.name)) {
              this.nameType = 'zfb';
              if (this.query.supportWay == 3) {
                this.payWay = ['','']
              } else if (this.query.supportWay == 2) {
                this.payWay = ['']
              } else if (this.query.supportWay == 1) {
                this.payWay = ['']
              }
            }else {
              this.payWay = ['']
            }
            this.query.id = res.data[this.$route.query.id].id;
            this.query.status = res.data[this.$route.query.id].status;
            this.query.accountId = res.data[this.$route.query.id].accountId;
            this.query.checkType = res.data[this.$route.query.id].checkType;
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      }
    },
    methods: {
      //
      create: function () {
        this.query.channelName = this.name;
        if (this.payWay.length == 2) {
          this.query.supportWay = 3;
        } else if (this.payWay.length == 1) {
          if (//.test(this.payWay[0])) {
            this.query.supportWay = 2;
          } else if (//.test(this.payWay[0])) {
            this.query.supportWay = 1;
          }else if (//.test(this.payWay[0])){
            this.query.supportWay = 4;
          }
        }
        var isFalse = false;
        for( var i in this.query){
           if(this.query[i]==''){
             isFalse = true;
             break;
           }else {
             isFalse = false
           }
        }
        this.$http.post('/admin/paymentChannel/add', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/admin/record/passList')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      },
      goBack: function () {
        this.$router.push('/admin/record/passList')
      },
      //
      change: function () {
        this.$http.post('/admin/channel/update', this.$data.query)
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/admin/record/passList')
          }, function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            });
          })
      }
    },
    watch: {
      name: function (val) {
        if(val.length!=0){
          if (//.test(val)) {
            this.nameType = 'wx'
          } else if (//.test(val)) {
            this.nameType = 'zfb'
          }else {
            this.nameType = 'yl'
          }
        } else {
          this.nameType = ''
        }
      }
    }
  }
</script>

