<template>
  <div>
    <div class="col-md-12" style="height: 880px">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title">T1</h3>
        </div>
        <div class="box-body">
          <!---->
          <ul class="search">
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.userNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.userName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-select clearable v-model="query.userType" size="small" style="width: 188px">
                <el-option label="" value=""></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-date-picker v-model="date" size="small" type="daterange" align="right" placeholder="" :picker-options="pickerOptions" style="width: 188px"></el-date-picker>
            </li>
            <li class="same">
              <label>:</label>
              <el-select clearable v-model="query.checkedStatus" size="small" style="width: 188px">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-select clearable v-model="query.settleStatus" size="small" style="width: 188px">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading" style="font-size: 12px;margin-bottom: 15px" :data="records" border @selection-change="handleSelectionChange">
            <!--<el-table-column type="selection" width="55"></el-table-column>-->
            <el-table-column prop="userNo" label="" ></el-table-column>
            <el-table-column prop="userName" label="" ></el-table-column>
            <el-table-column label="" >
              <template scope="scope">
                <span v-if="records[scope.$index].userType==2"></span>
                <span v-if="records[scope.$index].userType==1"></span>
                <span v-if="records[scope.$index].userType==3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="tradeDate" :formatter="changeTime" label=""></el-table-column>
            <el-table-column prop="tradeNumber" label="" align="right" width="90"></el-table-column>
            <el-table-column prop="settleAmount" label="" align="right" :formatter="changeNum"></el-table-column>
            <el-table-column prop="checkedStatusValue" label="" ></el-table-column>
            <el-table-column prop="settleStatusValue" label="" ></el-table-column>
            <el-table-column label="" width="70">
              <template scope="scope">
                <el-button @click.native.prevent="_$power(list,'boss_trade_export',scope.$index)" type="text" size="small" v-if="records[scope.$index].settleStatusValue!=''"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.pageNo"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
          <!---->
          <div v-if="isShow">
            <el-dialog title="" v-model="isShow">
              <div class="maskCon">
                <span></span>
                <span>{{records[this.$data.index].userName}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].userNo}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].settleAmount}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].tradeNumber}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].tradeDate}}</span>
              </div>
              <div slot="footer" class="dialog-footer" style="text-align: center;">
                <el-button @click="isShow = false"> </el-button>
                <el-button @click="settle(2,records[index].id)"></el-button>
                <el-button @click="settle(3,records[index].id)"></el-button>
              </div>
            </el-dialog>
          </div>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="babel">
    export default{
      name: 't1Audit',
      data(){
            return{
              pickerOptions: {
                shortcuts: [{
                  text: '',
                  onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', [start, end]);
                  }
                }, {
                  text: '',
                  onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                    picker.$emit('pick', [start, end]);
                  }
                }, {
                  text: '',
                  onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                    picker.$emit('pick', [start, end]);
                  }
                }]
              },
              date:'',
              records:[],
              count:0,
              total:0,
              query:{
                pageNo:1,
                pageSize:10,
                userNo:"",//
                userName:"",  //
                userType:'',
                startSettleDate:"",
                endSettleDate:"",
                checkedStatus:'',
                settleStatus:''
              },
              multipleSelection:[],
              currentPage4: 1,
              loading:true,
              isShow:false,
              index:'',
            }
        },
      created: function () {
        let time = new Date();
        this.date = [time,time];
        for (var j = 0; j < this.date.length; j++) {
          var str = this.date[j];
          var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
          for (var i = 0, len = ary.length; i < len; i++) {
            if (ary[i] < 10) {
              ary[i] = '0' + ary[i];
            }
          }
          str = ary[0] + '-' + ary[1] + '-' + ary[2];
          if (j == 0) {
            this.$data.query.startSettleDate = str;
          } else {
            this.$data.query.endSettleDate = str;
          }
        }
        this.getData()
      },
      methods: {
        changeTime: function (row, column) {
          var val = row.tradeDate;
          if (val == '' || val == null) {
            return ''
          } else {
            val = new Date(val)
            var year = val.getFullYear();
            var month = val.getMonth() + 1;
            var date = val.getDate();
            var hour = val.getHours();
            var minute = val.getMinutes();
            var second = val.getSeconds();

            function tod(a) {
              if (a < 10) {
                a = "0" + a
              }
              return a;
            }

            return year + "-" + tod(month) + "-" + tod(date) + " " + tod(hour) + ":" + tod(minute) + ":" + tod(second);
          }
        },
        changeNum: function (row, column) {
          var val = row.settleAmount;
          return parseFloat(val).toFixed(2);
        },
        getData: function () {
          this.loading = true;
          this.$http.post('/admin/settle/list',this.$data.query)
            .then(function (res) {
              this.$data.records = res.data.records;
              this.$data.count = res.data.count;
              this.$data.total = res.data.totalPage;
              this.$data.loading = false;
              var changeTime=function (val) {
                if(val==''||val==null){
                  return ''
                }else {
                  val = new Date(val)
                  var year=val.getFullYear();
                  var month=val.getMonth()+1;
                  var date=val.getDate();
                  function tod(a) {
                    if(a<10){
                      a = "0"+a
                    }
                    return a;
                  }
                  return year+"-"+tod(month)+"-"+tod(date);
                }
              }
              for(let i = 0; i < this.$data.records.length; i++){
                this.$data.records[i].tradeDate = changeTime(this.$data.records[i].tradeDate)
              }
            },function (err) {
              this.$data.loading = false;
              this.$message({
                showClose: true,
                message: err.statusMessage,
                type: 'error'
              })
            })
        },
        search: function () {
          this.$data.query.pageNo = 1;
          this.getData()
        },
        list: function (val) {
          this.$data.index = val;
          this.$data.isShow = true;
        },
        //
        handleSelectionChange(val) {
            console.log(val)
          this.multipleSelection = val;
        },
        //
        handleSizeChange(val) {
          this.$data.query.pageNo = 1;
          this.$data.query.pageSize = val;
          this.getData()
        },
        //
        handleCurrentChange(val) {
          this.$data.query.pageNo = val;
          this.getData()
        },
        //
        settle(val,id) {
          this.$http.post('/admin/settle/singleSettle',{recordId:id,option:val})
            .then(function (res) {
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              })
              this.$data.isShow = false
            })
            .catch(function (err) {
              this.$message({
                showClose: true,
                message: err.statusMessage,
                type: 'error'
              })
            })
        }
      },
      watch:{
        date:function (val,oldVal) {
          if(val!=undefined&&val[0]!=null){
            for(var j=0;j<val.length;j++){
              var str = val[j];
              var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
              for(var i = 0, len = ary.length; i < len; i ++) {
                if(ary[i] < 10) {
                  ary[i] = '0' + ary[i];
                }
              }
              str = ary[0] + '-' + ary[1] + '-' + ary[2];
              if(j==0){
                this.$data.query.startSettleDate = str;
              }else {
                this.$data.query.endSettleDate = str;
              }
            }
          }else {
            this.$data.query.startSettleDate = '';
            this.$data.query.endSettleDate = '';
          }
        }
      }
    }
</script>
<style scoped lang="less">
  ul {
    padding: 0;
    margin:0;
  }
  .search{
  margin-bottom:0;
  label{
    display: block;
    margin-bottom: 0;
  }
  }

  .maskCon{
    margin:0 0 15px 50px
  }
  .same{
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }
</style>



