<template>
  <div>
    <div class="col-md-12">
      <div class="box" style="margin-top:15px;overflow: hidden">
        <div class="box-header">
          <h3 class="box-title"></h3>
        </div>
        <div class="box-body">
          <!---->
          <ul class="search">
            <li class="same">
              <label>:</label>
              <el-date-picker style="width: 188px" v-model="date" size="small" type="daterange" align="right" placeholder="" :picker-options="pickerOptions"></el-date-picker>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.settleNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-select clearable v-model="query.settleStatus" size="small"style="width: 188px">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.userName" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <label>:</label>
              <el-input style="width: 188px" v-model="query.userNo" placeholder="" size="small"></el-input>
            </li>
            <li class="same">
              <div class="btn btn-primary" @click="search"></div>
            </li>
          </ul>
          <!---->
          <el-table v-loading.body="loading" style="font-size: 12px;margin-bottom: 15px" :data="records" border @selection-change="handleSelectionChange">
            <el-table-column type="index" width="62" label="" fixed="left"></el-table-column>
            <!--<el-table-column type="selection" width="55"></el-table-column>-->
            <el-table-column prop="userName" label="" ></el-table-column>
            <el-table-column prop="userNo" label="" ></el-table-column>
            <el-table-column label="" >
              <template scope="scope">
                <span v-if="records[scope.$index].appId=='hss'"></span>
                <span v-if="records[scope.$index].appId=='hsy'"></span>
              </template>
            </el-table-column>
            <el-table-column prop="settleNo" label="" ></el-table-column>
            <el-table-column prop="settleDate" label="" :formatter="changeTime"></el-table-column>
            <el-table-column prop="settleAmount" label="" :formatter="changeNum" align="right"></el-table-column>
            <el-table-column prop="settleModeValue" label="" ></el-table-column>
            <el-table-column prop="settleDestinationValue" label="" ></el-table-column>
            <el-table-column prop="settleStatusValue" label="" ></el-table-column>

            <!--<el-table-column label="" width="70">
              <template scope="scope">
                <el-button @click.native.prevent="list(scope.$index)" type="text" size="small" v-if="records[scope.$index].settleStatusValue!=''"></el-button>
              </template>-->
            </el-table-column>
          </el-table>
          <!---->
          <div class="block" style="text-align: right">
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="query.pageNo"
                           :page-sizes="[10, 20, 50]"
                           :page-size="query.pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="count">
            </el-pagination>
          </div>
          <!---->
          <div v-if="isShow">
            <el-dialog title="" v-model="isShow">
              <div class="maskCon">
                <span></span>
                <span>{{records[this.$data.index].userName}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].userNo}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].settleAmount}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].tradeNumber}}</span>
              </div>
              <div class="maskCon">
                <span></span>
                <span>{{records[index].tradeDate}}</span>
              </div>
              <div slot="footer" class="dialog-footer" style="text-align: center;">
                <el-button @click="isShow = false"> </el-button>
                <el-button @click="settle(2,records[index].id)"></el-button>
                <el-button @click="settle(3,records[index].id)"></el-button>
              </div>
            </el-dialog>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script lang="babel">
  export default{
    name: 'tAuditStore',
    data(){
      return{
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        date:'',
        records:[],
        count:0,
        total:0,
        query:{
          pageNo:1,
          pageSize:10,
          userNo:"",//
          userName:"",  //
          settleNo:"",//
          userType:2,//(23)
          starDate:"", // 
          endDate:"", //
          settleStatus:''
        },
        multipleSelection:[],
        currentPage4: 1,
        loading:true,
        isShow:false,
        index:'',
      }
    },
    created: function () {
      let time = new Date();
      this.date = [time,time];
      for (var j = 0; j < this.date.length; j++) {
        var str = this.date[j];
        var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
        for (var i = 0, len = ary.length; i < len; i++) {
          if (ary[i] < 10) {
            ary[i] = '0' + ary[i];
          }
        }
        str = ary[0] + '-' + ary[1] + '-' + ary[2];
        if (j == 0) {
          this.$data.query.starDate = str;
        } else {
          this.$data.query.endDate = str;
        }
      }
      this.getData()
    },
    methods: {
      changeTime: function (row, column) {
        var val = row.settleDate;
        if (val == '' || val == null) {
          return ''
        } else {
          val = new Date(val)
          var year = val.getFullYear();
          var month = val.getMonth() + 1;
          var date = val.getDate();
          var hour = val.getHours();
          var minute = val.getMinutes();
          var second = val.getSeconds();

          function tod(a) {
            if (a < 10) {
              a = "0" + a
            }
            return a;
          }

          return year + "-" + tod(month) + "-" + tod(date);
        }
      },
      changeNum: function (row, column) {
        var val = row.settleAmount;
        return parseFloat(val).toFixed(2);
      },
      getData: function () {
        this.loading = true;
        this.$http.post('/admin/settlementRecord/list',this.$data.query)
          .then(function (res) {
            this.$data.records = res.data.records;
            this.$data.count = res.data.count;
            this.$data.total = res.data.totalPage;
            this.$data.loading = false;
            var changeTime=function (val) {
              if(val==''||val==null){
                return ''
              }else {
                val = new Date(val)
                var year=val.getFullYear();
                var month=val.getMonth()+1;
                var date=val.getDate();
                function tod(a) {
                  if(a<10){
                    a = "0"+a
                  }
                  return a;
                }
                return year+"-"+tod(month)+"-"+tod(date);
              }
            }
            for(let i = 0; i < this.$data.records.length; i++){
              this.$data.records[i].tradeDate = changeTime(this.$data.records[i].tradeDate)
            }
          },function (err) {
            this.$data.loading = false;
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      },
      search: function () {
        this.$data.query.pageNo = 1;
        this.getData()
      },
      list: function (val) {
        this.$data.index = val;
        this.$data.isShow = true;
      },
      //
      handleSelectionChange(val) {
        console.log(val)
        this.multipleSelection = val;
      },
      //
      handleSizeChange(val) {
        this.$data.query.pageNo = 1;
        this.$data.query.pageSize = val;
        this.getData()
      },
      //
      handleCurrentChange(val) {
        this.$data.query.pageNo = val;
        this.getData()
      },
      //
      settle(val,id) {
        this.$http.post('/admin/settle/singleSettle',{recordId:id,option:val})
          .then(function (res) {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            })
            this.$data.isShow = false
          })
          .catch(function (err) {
            this.$message({
              showClose: true,
              message: err.statusMessage,
              type: 'error'
            })
          })
      }
    },
    watch:{
      date:function (val,oldVal) {
        if(val!=undefined&&val[0]!=null){
          for(var j=0;j<val.length;j++){
            var str = val[j];
            var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
            for(var i = 0, len = ary.length; i < len; i ++) {
              if(ary[i] < 10) {
                ary[i] = '0' + ary[i];
              }
            }
            str = ary[0] + '-' + ary[1] + '-' + ary[2];
            if(j==0){
              this.$data.query.starDate = str;
            }else {
              this.$data.query.endDate = str;
            }
          }
        }else {
          this.$data.query.starDate = '';
          this.$data.query.endDate = '';
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .maskCon{
    margin:0 0 15px 50px
  }
  ul {
    padding: 0;
    margin:0;
  }
  .search{
  margin-bottom:0;
  label{
    display: block;
    margin-bottom: 0;
  }
  }

  .same{
    list-style: none;
    display: inline-block;
    margin: 0 15px 15px 0;
  }
  .btn{
    font-size: 12px;
  }
</style>



